
// --- SCSS Variables ---
$primary-color: #0d6efd;
$secondary-color: #6c757d;
$success-color: #198754;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #0dcaf0;

$app-background-color: #f8f9fa;
$border-color: #dee2e6;
$text-color-primary: #212529;
$text-color-secondary: #495057;

$card-background-color: #DDEBF3;
$card-border-color: darken($card-background-color, 8%);

$base-font-size: 16px;
$border-radius-small: 8px;
$base-margin: 1rem;
$card-padding: 1.5rem;
$base-shadow: 0 4px 12px rgba(0, 0, 0, 0.07);

$font-family-sans-serif: 'Poppins', sans-serif;

// --- Base Styles ---
*, *::before, *::after { box-sizing: border-box; margin: 0; padding: 0; }
html { font-size: $base-font-size; }

.mobile-picking-interface {
    font-family: $font-family-sans-serif;
    background-color: $app-background-color;
    color: $text-color-primary;
    line-height: 1.6;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: $base-margin;
    justify-content: flex-start;
    padding-top: 2rem;
}

// --- Utility ---
.hidden { display: none !important; }

// --- Picking Card Styles ---
.picking-card {
    background-color: $card-background-color;
    border-radius: $border-radius-small;
    padding: $card-padding;
    box-shadow: $base-shadow;
    border: 1px solid $card-border-color;
    width: 100%;
    max-width: 450px;
    margin-bottom: 2rem;

    &__title {
        font-size: 1.5rem;
        font-weight: 700;
        color: darken($text-color-secondary, 15%);
        text-align: center;
        margin-bottom: 1.5rem;
    }
}

// --- Order Input Section ---
.order-input-section {
    margin-bottom: 1.5rem; // Space below the whole input area
    border-bottom: 1px solid $card-border-color; // Separator
    padding-bottom: 1.5rem; // Space above separator
}

// --- Order Scanning Section ---
#scan-order-section {
     text-align: center; // Center scan button and manual toggle
     .picking-button--scan {
         margin-bottom: 0.75rem; // Space between scan and manual toggle
     }
}

// --- Manual Order Input Section ---
#manual-order-section {
    // Styles applied when it's not hidden
    margin-top: 1rem; // Space above manual input when shown
    .input-group {
        display: flex;
        gap: 0.5rem;
        margin-bottom: 0.75rem; // Space below input/load group
    }
     .picking-input { // Ensure input takes space
        flex-grow: 1;
    }
}

// --- Scanner Container ---
.scanner-container {
    margin-top: 1rem;
    margin-bottom: 1rem;
    border: 1px dashed $secondary-color;
    border-radius: $border-radius-small;
    overflow: hidden;
    position: relative;

    #reader { width: 100%; background-color: #eee; }
    #reader video { display: block; width: 100%; height: auto; }
    #reader__dashboard_section_swaplink { display: none !important; }
    #reader__dashboard_section_csr { text-align: center; padding: 0.5rem; background-color: rgba(0,0,0,0.1); }
    #reader__dashboard_section_csr > span > button { display: none !important; }
}

.scanned-info {
    margin-top: 0.5rem;
    font-size: 0.9rem;
    color: $text-color-secondary;
    min-height: 1.2em;
    font-style: italic;
    text-align: center;
}

// --- Location Scan Prompt ---
.location-scan-prompt {
    margin-top: 1.5rem; margin-bottom: 1.5rem; padding: 1rem;
    border: 1px solid $info-color; border-radius: $border-radius-small;
    background-color: lighten($info-color, 45%); text-align: center;

    .prompt-text {
        font-size: 1.1rem; color: darken($info-color, 20%); margin-bottom: 1rem;
        strong { font-weight: 700; color: $primary-color; display: block; margin-top: 0.25rem; font-size: 1.3em; }
    }
}

// --- Input & Label Styles ---
.input-label {
    display: block; font-size: 0.875rem; font-weight: 500;
    color: $text-color-secondary; margin-bottom: 0.25rem;
}
.picking-input {
    border-radius: $border-radius-small; border: 1px solid $border-color;
    padding: 0.65rem 0.75rem; font-family: $font-family-sans-serif;
    font-size: 1rem; color: $text-color-primary; width: 100%;
    &:focus { border-color: $primary-color; box-shadow: 0 0 0 2px rgba($primary-color, 0.25); outline: none; }
    &[type=number] { -moz-appearance: textfield; &::-webkit-outer-spin-button, &::-webkit-inner-spin-button { -webkit-appearance: none; margin: 0; } }
}

// --- Button Styles ---
.picking-button {
    padding: 0.75rem 1.5rem; font-size: 1rem; font-weight: 600;
    border-radius: $border-radius-small; border: none; cursor: pointer;
    transition: background-color 0.2s ease, box-shadow 0.2s ease;
    font-family: $font-family-sans-serif; display: inline-flex;
    align-items: center; justify-content: center; text-decoration: none;
    white-space: nowrap; gap: 0.5rem;

    .material-icons-outlined { font-size: 1.3em; vertical-align: middle; }

    &--primary { background-color: $primary-color; color: white;
        &:hover { background-color: darken($primary-color, 10%); }
        &:focus { outline: none; box-shadow: 0 0 0 3px rgba($primary-color, 0.4); }
    }
    &--success { background-color: $success-color; color: white; width: 100%;
         &:hover { background-color: darken($success-color, 10%); }
         &:focus { outline: none; box-shadow: 0 0 0 3px rgba($success-color, 0.4); }
    }
    &--danger { background-color: $danger-color; color: white;
         &:hover { background-color: darken($danger-color, 10%); }
         &:focus { outline: none; box-shadow: 0 0 0 3px rgba($danger-color, 0.4); }
    }
     &--secondary { // For toggle buttons
        background-color: lighten($secondary-color, 45%);
        color: darken($secondary-color, 15%);
        border: 1px solid lighten($secondary-color, 30%);
        &:hover { background-color: lighten($secondary-color, 40%); }
        &:focus { outline: none; box-shadow: 0 0 0 3px rgba($secondary-color, 0.3); }
    }
    &--scan { width: 100%; } // Keep scan buttons full width
    &--small { padding: 0.5rem 1rem; font-size: 0.9rem; margin-top: 0.5rem; }
    &--full-width { width: 100%; } // Utility for toggle buttons
}

// --- Task Display Styles ---
.task-display { /* ... (unchanged) ... */ }
.icon-text { /* ... (unchanged) ... */ }
.details-text { /* ... (unchanged) ... */ }

// --- Confirmation Area Styles ---
.confirmation-area { /* ... (unchanged) ... */ }

// --- Message Area Styles ---
.message-area { /* ... (unchanged) ... */ }

// --- All Done Message Styles ---
.all-done-message { /* ... (unchanged) ... */ }

// --- Loading Overlay & Spinner ---
#loading-overlay { /* ... (unchanged) ... */ }
.spinner { /* ... (unchanged) ... */ }
@keyframes spin { /* ... (unchanged) ... */ }

.task-display { margin-top: 1.5rem; margin-bottom: 1.5rem; padding-top: 1rem; border-top: 1px solid $card-border-color; & > * { margin-bottom: 0.75rem; } & > *:last-child { margin-bottom: 0; } &__subtitle { font-size: 1.125rem; font-weight: 600; color: $text-color-primary; } &__product-info { font-size: 1.125rem; color: $text-color-primary; strong { font-weight: 600; } } &__location { font-weight: 500; color: $primary-color; } &__detail-value { font-weight: 500; color: $text-color-primary; } &__quantity-needed { font-size: 1.75rem; font-weight: 700; text-align: center; color: $success-color; margin-top: 0.5rem; } &__availability-info { font-size: 0.875rem; text-align: center; } }
.icon-text { display: flex; align-items: center; gap: 0.4rem; color: $text-color-secondary; font-size: 1rem; .material-icons-outlined { font-size: 1.3em; color: $secondary-color; flex-shrink: 0; } strong { color: $text-color-primary; font-weight: 500; } }
.details-text { color: $text-color-secondary; font-size: 0.875rem; }
.confirmation-area { margin-bottom: 1.5rem; .picking-input { margin-bottom: 1rem; } }
.message-area { min-height: 1.5em; text-align: center; font-size: 0.875rem; font-weight: 500; margin-top: 1rem; &--error { color: $danger-color; } &--success { color: $success-color; } }
.all-done-message { margin-top: 1.5rem; text-align: center; font-size: 1.25rem; font-weight: 600; color: $primary-color; .material-icons-outlined { font-size: 2.5rem; display: block; margin-bottom: 0.5rem; } }
#loading-overlay { position: fixed; inset: 0; background-color: rgba($app-background-color, 0.85); display: flex; align-items: center; justify-content: center; z-index: 50; }
.spinner { border: 4px solid rgba($text-color-primary, 0.1); width: 36px; height: 36px; border-radius: 50%; border-left-color: $primary-color; animation: spin 1s ease infinite; }
@keyframes spin { 0% { transform: rotate(0deg); } 100% { transform: rotate(360deg); } }